@import url(https://fonts.googleapis.com/css?family=Roboto:300);
@import url(https://fonts.googleapis.com/css?family=Roboto+Condensed:300);
.header{background-color:#fff;border-top-left-radius:4px;border-top-right-radius:4px}.header__title{border-top-left-radius:4px;border-top-right-radius:4px;background-color:#de3f53;color:#fff;font-family:'Roboto Condensed', Arial, sans-serif;font-size:20px;font-weight:100;line-height:56px;padding-left:15px;margin:0;text-transform:uppercase}.header__nav{display:flex;width:100%;align-items:center;justify-content:center}.header__nav-item{background:transparent;display:inline-block;vertical-align:top;width:100px;height:34px;line-height:36px;text-transform:uppercase;text-decoration:none;color:#333;letter-spacing:0.1em;text-align:center;font-size:0.8rem;margin:10px;position:relative;border:2px solid transparent}.header__nav-item:after{width:0%;height:2px;display:block;background-color:#333;content:' ';position:absolute;top:34px;left:50%;transition:left 0.2s cubic-bezier(0.215, 0.61, 0.355, 1),width 0.2s cubic-bezier(0.215, 0.61, 0.355, 1)}.header__nav-item:hover:after{width:100%;height:2px;display:block;background-color:#333;content:' ';position:absolute;top:34px;left:0}

.add-todo{display:flex;width:100%}.add-todo__input,.add-todo__submit{background-color:#171d37;color:#5e6a99;border:none;font-size:14px;margin:0;padding:20px 15px;transition:all 0.2s linear}.add-todo__input:focus,.add-todo__submit:focus{border:none;outline:none}.add-todo__input{width:100%}.add-todo__input:focus{color:#b1b2c9}.add-todo__submit{padding:20px 25px}.add-todo__submit:hover,.add-todo__submit:focus{color:#fff}.add-todo ::-webkit-input-placeholder{color:#464f72}.add-todo :-moz-placeholder{color:#464f72}.add-todo ::-moz-placeholder{color:#464f72}.add-todo :-ms-input-placeholder{color:#464f72}

ul {
  list-style: none;
  margin: 0px auto;
  width: 100%;
  padding: 0;
}

.todo-item{cursor:pointer;display:block;line-height:56px;padding:0 15px;position:relative}.todo-item__wrapper{list-style:none;margin:0;padding:0;background-color:#262e4c;border-bottom:1px solid #1c2340;color:#b1b2c9}.todo-item:hover{background-color:#2a3353;color:#8284a3}.todo-item:hover .todo-item__remove-btn{display:block}.todo-item:last-child{border-bottom-left-radius:4px;border-bottom-right-radius:4px}.todo-item__title{margin-left:30px;transition:all 0.2s linear}.todo-item__remove-btn{border:none;padding:0;background-color:#de3f53;border-radius:50%;color:#fff;display:none;float:right;font-weight:bold;line-height:normal;height:16px;margin-top:20px;text-align:center;text-decoration:none;position:relative;width:16px;transition:all 0.2s linear}.todo-item__remove-btn-text{position:absolute;top:-2px;left:5px}.todo-item__remove-btn:hover{background-color:#fff;color:#de3f53}.todo-item__remove-btn:hover{background-color:#fff;color:#de3f53}.todo-item__checkbox{cursor:pointer;opacity:0;position:absolute}.todo-item__checkbox+i{background-color:#404a6e;border-radius:50%;display:block;height:16px;position:absolute;top:20px;width:16px;z-index:1}.todo-item__checkbox:checked+i:after{background-color:#6e6e96;border-radius:50%;content:'';display:block;height:8px;left:4px;position:absolute;top:4px;width:8px;z-index:2}.todo-item__checkbox:checked ~ .todo-item__title{color:#586186;text-decoration:line-through}

.App{width:100%;display:flex;align-items:center;justify-content:center;flex-direction:column}.container{width:500px;margin:40px 20px 10px;box-shadow:5px 15px 20px 0 rgba(0,0,0,0.4);border-radius:4px}*{margin:0;padding:0;box-sizing:border-box}body{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;-webkit-font-smoothing:antialiased;-moz-osx-font-smoothing:grayscale}a,button{font-family:-apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

